.button {
  border: none;
  outline: none;
  cursor: pointer;
  height: 40px;
}

.submit {
  composes: button;
  width: 100%;
  display: block;
  /* padding: 10px 0; */
  /* margin-top: 25px; */
  font-size: 25px;
  font-weight: bold;
  color: white;
  background: #ff4343;
}

.submit:hover {
  background: #d61f1f;
}

.fancySubmit {
  composes: submit;
  position: relative;
  transition: 800ms ease all;
}

.fancySubmit:hover {
  background: #fff;
  color: #ff0040;
  /* color: #1d86bb; */
}

.fancySubmit:before,
.fancySubmit:after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 2px;
  width: 0;
  background: #ff0040;
  /* background: #1d86bb; */
  transition: 400ms ease all;
}
.fancySubmit:after {
  right: inherit;
  top: inherit;
  left: 0;
  bottom: 0;
}
.fancySubmit:hover:before,
.fancySubmit:hover:after {
  width: 100%;
  transition: 800ms ease all;
}

.backToBtn {
  composes: button;
  width: 170px;
}

.request {
  composes: fancySubmit;
  width: 115px;
  font-size: 20px;
}

.signOut {
  composes: button;
  color: #fff;
  background: #0c2966;
  width: 70px;
  height: 30px;
  box-shadow: 1px 1px 1px 1px rgb(163, 159, 159);
  font-size: 14px;
}

.signOut:hover {
  background: #22448f;
}

.createProfile {
  composes: button;
  width: 150px;
  /* padding: 10px 20px; */
  margin: 20px;
  background: #c56f82;
  border-radius: 5px;
  /* color: rgb(59, 57, 57); */
  color: #ffffff;
  font-weight: bold;
  cursor: pointer;
  margin-left: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.createProfile:hover {
  background: #945160;
}

.createProfile:active {
  background: rgb(20, 134, 168);
}

.filter {
  composes: createProfile;
  background: rgba(11, 100, 124, 0.658);
}

.filter:hover {
  background: #7cb2d1;
}

.delete_user {
  composes: createProfile;
  background: #cac5ea;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 15px;
}
.delete_user:hover {
  background: #dbb4da;
}

.choose {
  composes: createProfile;
  height: 30px;
  align-items: center;
  font-size: 11px;
}

