.card {
  position: absolute;
  top: 50%;
  left: 50%;
  min-width: 400px;
  transform: translate(-50%, -50%);
  border: 1px solid #fff;
  border-radius: 5px;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.3);
  padding: 20px;
}

.head {
  border-bottom: 1px solid #a6a6a6;
  display: flex;
  justify-content: center;
  padding-bottom: 10px;
  width: 300px;
  margin: 0 auto 15px;
}

.content {
  width: 300px;
  margin: 0 auto;
}

.logo {
  width: 130px;
  height: 150px;
}
