.itemContainer {
  display: flex;
  flex-direction: column;
  padding: 20px;
}
.itemContainerRow {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.itemBox {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 300px;
  height: auto;
  background: #ffffff;
  padding: 10px;
  margin: 0 10px 0 0;
  transition-property: background-color;
  transition-duration: 2s;
}

.itemBox:hover {
  background: #03668d;
  /* color: #ffffff; */
}
.itemBoxDiv {
  padding: 10px 0;
}
.itemTitle {
  /* font-style: italic; */
  font-weight: bold;
  margin: 0 0 5px 0;
}

.filesBox {
  display: flex;
  flex-direction: column;
  margin-left: 22px; 
  /* justify-content: flex-start; */
}

.filesHeading {
  margin: 20px 0 30px 0;
}

.deleteBtn {
  height: 20px;
  width: 20px;
  /* position: relative;
  top: -55px;
  left: 230px; */
  border: none;
  font-weight: bold;
  cursor: pointer;
  outline: none;
  margin-left: 5px;
}

.deleteBtn_img {
  height: 100%;
  width: 100%;
  background: #F2F3F7;
}

.linkBox{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 10px;
  /* margin-top: 30px; */
}

.fileInput{
  margin-bottom: 30px;
}

.fileLink{
  cursor: pointer
}

.fileLink:hover {
  color: gray;
}