.form {
  margin-left: 15px;
}

.inputBox {
  margin-bottom: 5px;
}
.label {
  font-size: 18px;
  margin-right: 5px;
  color: #036288;
}

.input {
  height: 25px;
  border: none;
  outline: none;
  border-bottom: 1px solid skyblue;
  background: #f2f3f7;
}
.input:active{
    border-bottom: 1px solid rgb(83, 188, 230);
}

.input::placeholder {
  color: #036288;
}

.btnBox {
  width: 200px;
  display: flex;
  justify-content: flex-end;
}

.submit {
  margin-right: 20px;
  outline: none;
  border: none;
  width: 50px;
  height: 25px;
  background: skyblue;
  color: #fff;
  cursor: pointer;
}

.submit:hover {
  background: rgb(91, 199, 241);
}
