.technologies_item {
  width: 100%;
  margin: 40px;
  height: auto;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.item_conteiner {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.technologies_links {
  color: #01556a;
  font-weight: bolder;
  background: #fff;
  outline: none;
  border-top: 15px solid #5c7598;
  border-radius: 15px;
  width: 250px;
  height: 120px;
  padding: 20px;
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 10px 9px 25px 0px rgba(0, 0, 0, 0.2);
}

.technologies_links:hover {
  border-top: 15px solid #6885ad;
  color: #05586d;
}

.technologytBTN {
  outline: none;
    border: none;
    background: #abb7c8;
    border-radius: 15px;
    /* padding: 10px 20px; */
    width: 190px;
    height: 43px;
    font-weight: bolder;
    font-size: 15px;
    color: #fff;
    margin-top: 30px;
    margin-left: 63px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.technologytBTN:hover {
  background: #dadadb;
}

.technologytBTN:active {
  background: #f7f0c7;
}

.deleteBtn {
  height: 25px;
  width: 25px;
  position: relative;
  top: -55px;
  left: 230px;
  border: none;
  font-size: 17px;
  font-weight: bold;
  cursor: pointer;
  outline: none;
  float: right;
}

.deleteBtn_img {
  height: 100%;
  width: 100%;
  background: #fff;
}

.plusIcon {
  width: 25px;
  height: 25px;
}
