.project_conteiner {
  width: 100%;
  margin: 40px 40px;
}

.item_conteiner {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.project_item {
  width: 80%;
  margin: 20px;
  height: auto;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.project_links {
  font-weight: bolder;
  background: #fff;
  outline: none;
  color: #3786c7;
  border-top: 15px solid #abc9e0;
  border-radius: 15px;
  width: 250px;
  height: 120px;
  padding: 20px;
  margin: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 10px 9px 25px 0px rgba(0, 0, 0, 0.2);
}

.project_links:hover {
  color: #84a8c5;
  border-top: 15px solid #d1d9dc;
}

.projectBTN {
  outline: none;
  border: none;
  background: #6d778d;
  width: 190px;
  height: 43px;
  border-radius: 15px;
  padding: 10px 20px;
  font-weight: bolder;
  font-size: 15px;
  color: #fff;
  margin-left: 40px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.plusIcon {
  width: 25px;
  height: 25px;
}

.projectBTN:hover {
  background: #5d6b8a;
}

.projectBTN:active {
  background: #80838d;
}

.deleteBtn {
  height: 25px;
  width: 25px;
  position: relative;
  top: -55px;
  left: 230px;
  border: none;
  font-size: 17px;
  font-weight: bold;
  cursor: pointer;
  outline: none;
  float: right;
}

.deleteBtn_img {
  height: 100%;
  width: 100%;
  background: #fff;
}
