.modalWrapper {
  background: #f4f4f4;
  border: 1px solid #d0cccc;
  box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 7px 20px 0 rgba(0, 0, 0, 0.17);
  margin: 100px auto;
  transition: all 0.8s;
  width: 600px;
  height: 490px;
  opacity: 0.4;
}

.CtreateUser_form {
  margin: 30px;
}

.cont {
  display: flex;
  flex-direction: column;
  margin: 25px;
}
.cont_last_child {
  display: flex;
  margin-top: 100px;
  justify-content: space-between;
  margin: 5px 20px;
  /* padding-top: 60px; */
  align-items: flex-end;
}

.CreateUser_label {
  color: rgb(28, 28, 29);
  margin-bottom: 15px;
  /* font-weight: bolder; */
}

.CreateUser_input {
  outline: none;
  border: none;
  border-bottom: 1px solid #e7cbcb;
  background: transparent;
  width: 90%;
  height: 25px;
  display: block;
  color: #005a75;
}

.normalBTN {
  outline: none;
  border: none;
  width: 80px;
  height: 35px;
  cursor: pointer;
  font-weight: bold;
}

.CreateUserBTNs {
  composes: normalBTN;
  /* background: #04be67; */
}

.CencelUserBTNs {
  composes: normalBTN;
}

.normalBTN:hover {
  background: #b0e9ec;
}

.normalBTN:active {
  background: #b0caec;
}

.error_msg {
  display: flex;
  flex-wrap: wrap;
  font-size: 13px;
  color: red;
}

.CreateUser_input::placeholder {
  color: #b0caec;
}

.margin {
  margin-bottom: 30px;
}
