.usersBox {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 20px;
  flex-direction: column;
}

.username {
  /* width: 200px; */
  font-weight: bold;
}

.email {
  font-weight: bold;
}

.img {
  width: 200px;
  padding: 20px;
}

.createBox {
  height: 50px;
}
.editForm {
  z-index: 2;
}

.conteiner {
  margin-top: 40px;
  padding: 0 30px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.map_conteiner {
  border-radius: 5px;
  margin: 20px;
  width: 400px;
  height: 400px;
  display: flex;
  justify-content: center;
  -webkit-box-shadow: 10px 14px 5px 0px rgba(0, 0, 0, 0.3);
  -moz-box-shadow: 10px 14px 5px 0px rgba(0, 0, 0, 0.3);
  box-shadow: 8px 12px 20px 0px #88bdc54d, -8px -12px 20px 0px #88bdc54d;
}

.normalBTN {
  outline: none;
  border: none;
  cursor: pointer;
}

.edditBTN {
  composes: normalBTN;
  margin-top: 15px;
  width: 150px;
  height: 40px;
  color: #fff;
  font-weight: bold;
  font-size: 17px;
  background: #e1a9c2;
  display: flex;
  justify-content: center;
  align-items: center;
  /* justify-content: space-between; */
}

.edditBTN:hover {
  background: #dbb4da;
}

.userEdit {
  width: 25px;
  height: 25px;
}
.deleteUser {
  width: 20px;
  height: 20px;
}

.addUserIcon {
  width: 25px;
  height: 25px;
}
