.flex {
  display: flex;
}
a {
  text-decoration: none !important;
}
.sidebar {
  composes: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 120px;
  height: 90vh;
  transition: 0.6s ease-in-out;
  flex-shrink: 1;
  box-sizing: border-box;
  margin-left: 0;
  /* background: #03668D; */
}

.sidebarHidden {
  composes: sidebar;
  transition: 0.6s ease-in-out;
  /* background: #011133; */
  margin-left: -120px;
}
.list {
  width: 100%;
  height: 90vh;
  composes: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 10px 0;
  background: #fff;
  margin-top: 89px;
}
.list_item {
  composes: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  /* margin: 10px 0; */
  height: 61px; /*40px*/
  color: #50514f;
  border-bottom: 1px solid #f2f3f7;
}
.list_item:hover {
  background: #eff7ff;
  font-weight: bold;
}

.version {
  composes: flex;
  justify-self: start;
  justify-content: center;
  color: rgb(202, 228, 228);
}
.menuBtn {
  border: none;
  outline: none;
  background: red;
  width: 40px;
  height: 40px;
  margin-top: 10px;
  margin-left: 10px;
  display: flex;
}

.blueCont {
  composes: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  width: 80px;
  height: 91vh;
  z-index: 1;
  background: #007d92;
}

.blueCont_item {
  width: 40px;
  margin: 10px;
  height: 40px;
  cursor: pointer;
}

.blueCont_item:first-child {
  margin-bottom: 40px;
  margin-top: 10px;
}

.blueCont_item img {
  width: 40px;
  margin: 10px;
  height: 40px;
}

.blueCont_item img:hover {
  width: 40px;
  margin: 10px;
  height: 40px;
  transform: scale(1.1);
}

.blueCont_item_version {
  position: absolute;
  bottom: 0px;
  color: #fff;
  background: #007d92;
  padding: 15px;
}

.margin {
  margin-bottom: 20px;
}

.version_text {
  font-style: italic;
}

