.formContainer {
  z-index: 999;
  position: absolute;
  top: 100px;
  left: 600px;
  background: #f4f4f4;
  width: 500px;
  height: auto;
  padding: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
  -webkit-box-shadow: -6px -1px 6px 5px rgba(0, 0, 0, 0.36);
  box-shadow: -6px -1px 26px 5px rgba(0, 0, 0, 0.36);
}
.formContainer input,
input[type='date'] {
  outline: none;
}

.formContainer__item {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  align-items: space-between;
}

.formContainer__input {
  display: flex;
  justify-content: space-between;
  width: 50%;
  height: auto;
  margin: 10px 0;
  border: none;
  border-radius: 4px;
  border-bottom: 1px solid #cccccc;
  background: transparent;
  color: rgb(28, 28, 29);
}

.formContainer__input input[type='date'] {
  border: none;
  border-right: 0.5px solid #cccccc;
}
.Btn {
  width: 70px;
  height: 40px;
  color: #ffffff;
  border: none;
  background: #000000;
  cursor: pointer;
}
.Btn:hover {
  background: #99d4d4;
}
.xBtn {
  width: 20px;
  height: 20px;
  background-image: url('../../../assets/images/xicon.png');
  background-size: cover;
  border: none;
}
.filterBtn {
  composes: Btn;
  background: #cbd8dd;
  color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
}
.clearBtn {
  composes: Btn;
  background: #cbd8dd;
  color: #000000;
}
.error {
  color: red;
  padding: 10px 0;
}
.widthMax {
  width: 100% !important;
  border-radius: 0;
  border: none !important;
  background: #F4F4F4;
}

.label,
.title {
  color: rgb(28, 28, 29);
}